export class HttpClient {
    constructor() {
        this.baseUrl = __PROD__ ? __SERVER_URL__ : "http://localhost:5000";
        this.headers = {
            "Content-Type": "application/json",
        };
    }
    static getInstance() {
        if (!HttpClient.instance) {
            HttpClient.instance = new HttpClient();
        }
        return HttpClient.instance;
    }
    async get(url, params = {}) {
        const processedBaseUrl = this.processUrl(this.baseUrl);
        const processedUrl = this.processUrl(url);
        const search = new URLSearchParams(params);
        const response = await fetch(`${processedBaseUrl}/${processedUrl}?${search}`, {
            method: "GET",
            credentials: "include",
        });
        return response.json();
    }
    async post(url, data = {}, params = {}, headers = this.headers) {
        const processedBaseUrl = this.processUrl(this.baseUrl);
        const processedUrl = this.processUrl(url);
        const search = new URLSearchParams(params);
        const response = await fetch(`${processedBaseUrl}/${processedUrl}?${search}`, {
            method: "POST",
            headers,
            credentials: "include",
            body: JSON.stringify(data),
        });
        return response.json();
    }
    processUrl(url) {
        let processedUrl = url;
        if (url.startsWith("/")) {
            processedUrl = url.slice(1);
        }
        if (processedUrl.endsWith("/")) {
            processedUrl = processedUrl.slice(0, -1);
        }
        return processedUrl;
    }
}
export const api = HttpClient.getInstance();
